import PropTypes from 'prop-types';
import {
    Box,
    Typography,
    Card,
    CardHeader,
    CardContent,
    Divider,
    Avatar,
    IconButton,
    Button,
    CardActions,
    Link,
    styled,
    Stack,
    Menu,
    MenuItem,
    Chip,
    Grid
} from '@mui/material';
import 'photoswipe/dist/photoswipe.css'
import MoreHorizTwoToneIcon from '@mui/icons-material/MoreHorizTwoTone';
import ShareTwoToneIcon from '@mui/icons-material/ShareTwoTone';
import Text from 'components/Text';
import ReactTimeAgo from 'react-time-ago'
import React, { useEffect, useState } from 'react';
import CommentTwoToneIcon from '@mui/icons-material/CommentTwoTone';
import VoteButton from 'components/buttons/VoteButtons';
// import ImageList from '@mui/material/ImageList';
// import ImageListItem from '@mui/material/ImageListItem';
import Ranking from 'components/Ranking';

import QuestionAnswerIcon from '@mui/icons-material/QuestionAnswer';
import PollIcon from '@mui/icons-material/Poll';
// import DownloadIcon from '@mui/icons-material/Download';
import DownloadForOfflineIcon from '@mui/icons-material/DownloadForOffline';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import { StatusCode, StatusList } from 'links/constant';
import { CopyToClipboard } from '_services';
import { getDownload } from '_services';

import { Gallery, Item } from 'react-photoswipe-gallery'

const CardActionsWrapper = styled(CardActions)(
    ({ theme }) => `
       background: ${theme.colors.alpha.black[5]};
       padding: ${theme.spacing(3)};
  `
);


function Post({ post, onMenu, onVote, userModel, viewPost, isOpen, toaster }) {
    // const [paction, setPaction] = useState({
    //     votes: post.votes,
    //     hasVote: post.hasVote,
    //     review: post.review
    // });


    const [anchorEl, setAnchorEl] = useState(null);
    const [gsize, setGsize] = useState({});
    const open = Boolean(anchorEl);
    const handleOptionClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleOptionClose = () => {
        setAnchorEl(null);
    };
    const handleOptionAction = (type) => {
        onMenu(post._id, type)
        setAnchorEl(null);
    }

    useEffect(() => {
        setTimeout(() => {
            let cgsize = {};
            post.meta.map((j, i) => {
                const e = document.getElementById('gs' + i);
                if (e.complete) {
                    cgsize = { ...cgsize, ['gs' + i]: { w: e.naturalWidth, h: e.naturalHeight } }
                } else {
                    cgsize = { ...cgsize, ['gs' + i]: { w: 1000, h: 801 } }
                }
            })
            setGsize({ ...cgsize });
        }, 2000)
    }, []);


    let tagsList = [];
    if (post.tags !== null) {
        tagsList = post.tags.split(",").map(hash => {
            return <><Link href="#" underline="hover">
                {hash}
            </Link>{" "}</>
        })
    }

    const ImageView = () => {
        if (post.meta.length == 0) {
            return "";
        }
        const smallItemStyles = {
            cursor: 'pointer',
            objectFit: 'cover',
            width: '150px',
            height: '150px',
        }
        return (<CardContent>
            <Gallery>
                <Box
                    sx={{
                        display: 'grid',
                        gridTemplateColumns: {
                            xs: 'repeat(2, 0fr)', sm: 'repeat(4, 0fr)', md: 'repeat(5, 0fr)', lg: 'repeat(5, 0fr)'
                        },
                        gridGap: 10,
                    }}
                >
                    {post.meta.map((item, index) => {
                        return (<Item
                            key={item.img}
                            cropped
                            original={item.img}
                            thumbnail={item.img}
                            height={('gs' + index in gsize) ? gsize['gs' + index].h : 801}
                            width={('gs' + index in gsize) ? gsize['gs' + index].w : 1000}
                        >
                            {({ ref, open }) => (
                                <img
                                    id={'gs' + index}
                                    style={smallItemStyles}
                                    ref={ref} onClick={open}
                                    src={item.img}
                                />
                            )}
                        </Item>)
                    })}
                </Box>
            </Gallery>
        </CardContent >)
    }
    return (
        <Box mb={2}>
            <Card>
                <CardHeader
                    avatar={post.statusCode == StatusCode.REVIEW ? <Avatar src={post.user_dp} /> : <></>}
                    action={
                        <>
                            {post.statusCode == StatusCode.REVIEW && (
                                <>
                                    <IconButton
                                        color="primary"
                                        onClick={handleOptionClick}
                                    >
                                        <MoreHorizTwoToneIcon fontSize="medium" />
                                    </IconButton>
                                    <Menu
                                        anchorEl={anchorEl}
                                        open={open}
                                        onClose={handleOptionClose}
                                    >
                                        {post.userid === userModel._id ? (
                                            [
                                                <MenuItem key={'delete'} onClick={() => { handleOptionAction(2) }}>Delete</MenuItem>
                                            ]
                                        ) : (
                                            <MenuItem key={'report'} onClick={() => { handleOptionAction(0) }}>Report</MenuItem>
                                        )}
                                    </Menu>
                                </>
                            )}

                        </>
                    }
                    titleTypographyProps={{ variant: 'h4' }}
                    subheaderTypographyProps={{ variant: 'subtitle2' }}
                    title={post.statusCode == StatusCode.REVIEW ? post.username : ""}
                    subheader={
                        <>
                            {tagsList}
                        </>
                    }
                />
                <Box px={3} pb={2}>
                    <Typography variant="h4" fontWeight="normal">
                        {post.post_detail}
                    </Typography>
                </Box>
                <ImageView />
                <Box p={1}>
                    <Typography variant="subtitle2">
                        <ReactTimeAgo date={new Date(post.create_at)} locale="en-US" />
                        {' | '}Supporters{' : '}
                        <Text>
                            <b>{post.supporters}</b>
                        </Text>
                    </Typography>
                </Box>
                <Divider />
                <CardActionsWrapper
                    sx={{
                        display: { xs: 'flex', md: 'flex' },
                        py: 1.5,
                        alignItems: 'center',
                        justifyContent: 'space-between'
                    }}
                >
                    <Grid
                        container
                        direction={'row'}
                        spacing={1}
                        justifyContent="space-between"
                        alignItems="center"
                    >
                        <Grid item xl={6} >
                            <Grid container direction={'row'}>
                                <Grid item xl={6}>
                                    {isOpen ? (
                                        <>
                                            {post.statusCode == StatusCode.VOTING && (
                                                <VoteButton post={post}
                                                    sx={{ mr: { xs: 0.5, md: 2 } }}
                                                    onClick={onVote} hasVote={post.hasVote} />
                                            )}
                                        </>
                                    ) : (
                                        <Button
                                            startIcon={<CommentTwoToneIcon />}
                                            variant="outlined"
                                            sx={{ mr: { xs: 0.5, md: 2 } }}
                                            onClick={() => {
                                                viewPost(post._id)
                                            }}
                                        >
                                            View
                                        </Button>
                                    )}
                                </Grid>
                                <Grid item xl={6}>
                                    <Button
                                        startIcon={<ShareTwoToneIcon />}
                                        variant="outlined"
                                        onClick={() => {
                                            CopyToClipboard(post.link)
                                            toaster('Link copied!!!')
                                        }}
                                    >
                                        Share
                                    </Button>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xl={6} >
                            <div style={{
                                display: 'flex',
                                alignItems: 'center',
                                flexWrap: 'wrap',
                            }}>
                                <Text
                                    sx={{ display: 'flex', mr: 1 }}
                                    color={StatusList[post.statusCode].color}
                                >
                                    {StatusList[post.statusCode].icon}{post.status}
                                </Text>{'|'}
                                <Text
                                    sx={{ display: 'flex', mx: 1 }}
                                >
                                    <QuestionAnswerIcon sx={{ mr: 1 }} />{post.review}
                                </Text>
                                {post.statusCode > StatusCode.VOTING && (
                                    <>{'|'}
                                        <Text
                                            sx={{ display: 'flex', mx: 1 }}
                                        >
                                            <PollIcon sx={{ mr: 1 }} />{post.tot_votes}
                                        </Text>{'|'}
                                        <Text
                                            sx={{ display: 'flex', mx: 1 }}
                                        >
                                            <ThumbUpIcon sx={{ mx: 1 }} />{post.votes}
                                        </Text>{'|'}
                                        <Ranking sx={{ mx: 1 }} voters={post.tot_votes} votes={post.votes} />
                                        {'|'}
                                        <a
                                            style={{ textDecoration: 'none', color: '#6E759F' }}
                                            href={post.dlink}
                                            onClick={(e) => {
                                                e.preventDefault();
                                                getDownload(post.dlink)
                                            }}
                                        >
                                            <DownloadForOfflineIcon sx={{ ml: 1, mt: '4px' }} />
                                        </a>
                                    </>
                                )}
                            </div>
                        </Grid>
                    </Grid>
                </CardActionsWrapper>
            </Card>
        </Box >
    );
}


Post.propTypes = {
    post: PropTypes.object.isRequired,
    onMenu: PropTypes.func.isRequired,
    userModel: PropTypes.object.isRequired,
    viewPost: PropTypes.func.isRequired,
    onVote: PropTypes.func,
    isOpen: PropTypes.bool
}

Post.defaultProps = {
    isOpen: false,
}
export default Post;  